import React, {Component} from 'react';
import styled from 'styled-components';
import { ReactComponent as Hamburger} from './assets/hamburger.svg'
const Wrapper = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const List = styled.ul`
  padding: 20px 30px;
  border: 1px solid black;
  background-color: lightgray;
`;

const ListItem = styled.li`
  margin: 10px 0;
  list-style: none;
  display: grid;
  grid-auto-flow: column;
  justify-content: left;
  grid-column-gap: 10px;
  align-items: center;
`;

const Dragger = styled.div``;

const StyledHamburger = styled(Hamburger)`
  width: 25px;
`;

class App extends Component {
    state = {
        items: ['Bread', 'Mustard', 'Avocado', 'Pasta', 'Eggs'],
        draggedItem: null,
    };


    handleDragStart = index => e => {
        e.dataTransfer.effectAllowed = "move"
        e.dataTransfer.setData("text/html", e.target.parentNode);
        e.dataTransfer.setDragImage(e.target.parentNode, 20, 20);

        this.setState({draggedItem: this.state.items[index]})
    };

    handleDragEnd = () => {
        this.setState({draggedItem: null});
    };

    handleDragOver = index => e => {
        e.preventDefault();
      const draggedOverItem = this.state.items[index];

      if (this.state.draggedItem === draggedOverItem) return;

      const items = this.state.items.filter(item => item !== this.state.draggedItem);

      items.splice(index, 0, this.state.draggedItem);

      this.setState({items})
    };

    handleDragEnter = e => e.preventDefault();

    render() {
        return (
            <Wrapper>
                <List>

                    {this.state.items.map((item, index) =>
                        <ListItem onDragEnter={this.handleDragEnter} onDragOver={this.handleDragOver(index)}>
                            <Dragger draggable
                                onDragStart={this.handleDragStart(index)}
                                onDragEnd={this.handleDragEnd}>
                                <StyledHamburger/>
                            </Dragger>
                            <span>{item}</span>
                        </ListItem>)}
                </List>
            </Wrapper>
        )
    };
}

export default App;
